import { useEffect, useRef, useState } from "react";
import { connect } from "react-redux";
import imgRemove from "../../../../assets/imgs/remove.png"


const ReceiptRow = ({
    receipt,
    expenseTypes,
    consolidateReceipts,
    submitReceiptData,
    deleteReceipt,
    updateReceiptAmt,
    userMode,
    enableInputFields
}) => {
    let [receiptNum, setReceiptNum] = useState("");
    let [date, setDate] = useState("");
    let [details, setDetails] = useState("");
    let [expenseType, setExpenseType] = useState("");
    let [amount, setAmount] = useState(0);

    // Consolidate receipts once submit button in claim details is clicked
    useEffect(()=>{
        if(consolidateReceipts === true) {
            let convertedDate = Date.parse(date);
            let newReceipt = {
                receiptCount: receipt.receiptCount,
                receiptNum: receiptNum,
                receiptDate: convertedDate,
                receiptDetails: details,
                expenseType: expenseType,
                receiptAmount: amount
            };
            if (receipt._id !== undefined) {
                newReceipt._id = receipt._id
            }
            submitReceiptData(newReceipt);
        };
    }, [
        receipt.receiptCount,
        receiptNum,
        date,
        details,
        expenseType,
        amount,
        consolidateReceipts,
        submitReceiptData,
        receipt._id,
    ]);

    // Display receipt details
    let receiptCopy = useRef(receipt).current
    useEffect(() => {
        if (userMode !== "create" && receiptCopy._id !== undefined) {
            let receiptDateCopy = new Date(receiptCopy.receiptDate)
            let year = receiptDateCopy.getFullYear();
            let month = receiptDateCopy.getMonth() + 1
            if (month < 10) {
                month = "0" + month
            }
            let date = receiptDateCopy.getDate();
            if (date < 10) {
                date = "0" + date
            }
            let formattedDate = `${year}-${month}-${date}`
            setReceiptNum(receiptCopy.receiptNum);
            setDate(formattedDate);
            setDetails(receiptCopy.receiptDetails);
            setExpenseType(receiptCopy.expenseType);
            setAmount(receiptCopy.receiptAmount);
        }
    }, [
        userMode,
        receiptCopy
    ])

    const rmvBtnHandler = () => {
        deleteReceipt(receipt)
    };
    const amtChangeHandler = (e) => {
        let amt = e.target.value * 1
        setAmount(amt);
        updateReceiptAmt(receipt.receiptCount, amt);
    };

    return(
        <div className="receipt-row">
            <div>
                <div>Receipt Number</div>
                <input
                    type = "text"
                    value = {receiptNum}
                    onChange = {e => setReceiptNum(e.target.value)}
                    disabled = {!enableInputFields}
                />
            </div>
            <div>
                <div>Date</div>
                <input 
                    type = "date"
                    value = {date}
                    onChange = {e => setDate(e.target.value)}
                    disabled = {!enableInputFields}
                />
            </div>
            <div className={!enableInputFields ? "adj-details" : null}>
                <div>Details</div>
                <input
                    type = "text"
                    value = {details}
                    onChange = {e => setDetails(e.target.value)}
                    disabled = {!enableInputFields}
                />
            </div>
            <div>
                <div>Expense Type</div>
                <select
                    value = {expenseType}
                    onChange = {e => setExpenseType(e.target.value)}
                    disabled = {!enableInputFields}
                >
                    <option value="" disabled defaultValue></option>
                    {expenseTypes.map(el => 
                        <option key = {el._id}>{el.expenseType}</option>
                    )}
                </select>
            </div>
            <div>
                <div>Amount</div>
                <input
                    type = "number"
                    value = {amount}
                    onChange = {amtChangeHandler}
                    disabled = {!enableInputFields}
                />
            </div>
            {enableInputFields ? 
            <div className="remove-rcpt-div">
                <button onClick={rmvBtnHandler}>
                    <img
                        src = {imgRemove}
                        alt = "remove"
                    />
                </button>
            </div>
            : null}
        </div>
    )
}


const mapStateToProps = (state) => {
    return {
        receipts: state.claimsSlice.receipts,
        consolidateReceipts: state.claimsSlice.consolidateReceipts,
        claimForViewing: state.viewClaimsSlice.claimForViewing,
        userMode:state.navSlice.userMode,
        months: state.navSlice.months
    }
};
const mapDispatchToProps = (dispatch) => {
    return {
        updateReceiptAmt: (receiptCount, amt) => dispatch({
            type: "UPDATE_RECEIPT_AMT",
            payload: {
                receiptCount,
                amt
            }
        }),
        computeAmtTotal: (receiptCount, amt) => dispatch({
            type: "COMPUTE_AMT_TOTAL", 
            payload: {
                receiptCount,
                amt
            }
        }), 
        submitReceiptData: (receipt) => dispatch({
            type: "SUBMIT_RECEIPT_DATA",
            payload: receipt
        }),
        deleteReceipt: (receipt) => dispatch({
            type: "DELETE_RECEIPT",
            payload: receipt
        }),
        setUserMode: (mode) => dispatch({
            type: "SET_USER_MODE",
            payload: mode
        })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ReceiptRow);