import Login from "./Login";
import Registration from "./Registration";
import {connect} from "react-redux";
import { useState } from "react";
import logo from "../../../assets/imgs/logo.png"
import logoLarge from "../../../assets/imgs/logo-large.png"

const LandingPage = (props) => {
    let [subPage, setSubPage] = useState("Login");

    return(
        <div id="landing-page">
            <div className="welcome-msg">
                <div>
                    <img 
                        src = {logo}
                        alt = "logo"
                        className = "small-logo"
                    />
                    <img 
                        src = {logoLarge}
                        alt = "logo"
                        className = "large-logo"
                    />
                </div>
            </div>
            <div>
                <div>
                    Here to return <br/>those precious <br/>coins!
                </div>
            </div>
            {subPage === "Login" ? <Login setSubPage={setSubPage}/> : null}
            {subPage === "Register" ? <Registration setSubPage={setSubPage}/> : null}
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        activePage: state.navSlice.activePage
    }
};
const mapDispatchToProps = (dispatch) => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LandingPage);