import logo from "../../assets/imgs/logo.png"
import logoLarge from "../../assets/imgs/logo-large.png"

const Banner = (props) => {
    return(
        <div className="banner-holder">
            <div id="banner">
                <div className="logo-cont">
                    <img 
                        src = {logo}
                        alt = "logo"
                        className = "small-logo"
                    />
                    <img 
                        src = {logoLarge}
                        alt = "logo"
                        className = "large-logo"
                    />
                </div>
            </div>
        </div>
    )
}

export default Banner;