const formatDate = (submittedDate) => {
    let months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]   
    let date = new Date(submittedDate);
    let month = months[date.getMonth()];
    let day = date.getDate();
    let year = date.getFullYear().toString().slice(2);
    let hour = date.getHours();
    let mins;
    date.getMinutes() < 10 ? mins = '0' + date.getMinutes() : mins = date.getMinutes();
    let formattedDate;
    let now = new Date();
    if (now.getFullYear() !== date.getFullYear()){
        formattedDate = `${month}-${day}-${year}, ${hour}:${mins}`
    }
    else if (now.getMonth() === date.getMonth() && now.getDate() === day) {
        formattedDate = `Today, ${hour}:${mins}`
    }
    else {
        formattedDate = `${month}-${day}, ${hour}:${mins}`
    }
    return formattedDate
}

module.exports = {
    formatDate
}