import { useState, useEffect } from "react";
import { connect } from "react-redux";
import Axios from "axios";
import ClaimRow from "./ClaimRow";

const ClaimList = ({
    backendPort,
    currentUser,
    showClaims,
    claimsList,
    appName,
}) => {

    let [claimForPullOut, setClaimForPullOut] = useState({});
    let [modalDisp, setModalDisp] = useState({
        display: false,
        msg: "",
        msgDetails: "",
        modalAction: ""
    });

    useEffect(() => {
        Axios.get(backendPort + "claims", {headers: {authorization: currentUser.access}})
        .then(res => 
            showClaims(res.data))
    }, [backendPort, showClaims, currentUser.access, claimForPullOut])

    const backBtnHandler = () => {
        setModalDisp({
            display: false,
            msg: "",
            msgDetails: "",
            modalAction: ""
        });
        setClaimForPullOut({});
    };
    const proceedBtnHandler = () => {
        let claimCopy = {...claimForPullOut};
        Axios.put(backendPort + "claims/pullout/" + claimCopy._id, claimCopy, {headers: {authorization: currentUser.access}})
        .then( res => {
            if (res.data !== "invalid") {
                setModalDisp({
                    display: true,
                    msg: "Claim pulled out.",
                    msgDetails: `Claim Id: ${res.data._id}, Pulled out: ${res.data.updatedAt}`,
                    modalAction: "notifyPullOutSuccess"
                });
                setClaimForPullOut({});
            }
            else {
                setModalDisp({
                    display: true,
                    msg: "Unsuccessful.",
                    msgDetails: "",
                    modalAction: "notifyPullOutFailure"
                })
            }
        })
    };
    return(
        <div id="claim-list-section">
            <h2>Claims List</h2>
            <div className="view-claims-row view-claims-row-header">
                <div>Claim Id</div>
                <div>Purpose</div>
                <div>Status</div>
                <div>Proponent</div>
                <div>Amount</div>
                <div>Date Modified</div>
                <div></div>
            </div>
            <div className="claim-rows-div">
                {
                    claimsList.length === 0 ?
                    <div className="no-claims">No claims in the list.</div>
                    :
                    claimsList.map(claim => 
                        <ClaimRow
                            key = {claim._id} 
                            claim = {claim}
                            setModalDisp = {setModalDisp}
                            setClaimForPullOut = {setClaimForPullOut}
                        />)
                }
            </div>
            {
                modalDisp.display ?
                <div className="modal-container">
                    <div className="modal">
                        <div>{appName}</div>
                        <div className="modalbody">
                            <div>
                                <div>{modalDisp.msg}</div>
                                <div>{modalDisp.msgDetails}</div>
                            </div>
                            <div className="modal-btn-div">
                                {modalDisp.modalAction === "confirmPullOut" ?
                                    <>
                                        <button onClick={proceedBtnHandler}>Proceed</button>
                                        <button onClick={backBtnHandler}>Back</button>
                                    </>
                                    : null
                                }
                                {modalDisp.modalAction === "notifyPullOutSuccess" ?
                                    <button onClick={backBtnHandler}>Back</button>
                                    : null
                                }
                                {modalDisp.modalAction === "notifyPullOutFailure" ?
                                    <>
                                        <button onClick={proceedBtnHandler}>Retry</button>
                                        <button onClick={backBtnHandler}>Back</button>
                                    </>
                                    : null
                                }
                            </div>
                        </div>
                    </div>
                </div>
                : null
            }
        </div>
    )
}
const mapStateToProps = (state) => {
    return {
        appName: state.navSlice.appName,
        backendPort: state.navSlice.backendPort,
        currentUser: state.loginSlice.currentUser,
        claimsList: state.viewClaimsSlice.claimsList
    }
};
const mapDispatchToProps = (dispatch) => {
    return{
        showClaims: (claims) => dispatch({
            type: "SHOW_CLAIMS",
            payload: claims
        })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClaimList);