import { useState, useEffect } from "react";
import { connect } from "react-redux";
import Axios from "axios";

const UserEdit = ({user, backendPort, setSubPage, setUserForEdit, currentUser, userList, setUserList}) => {
    let [username, setUsername] = useState(user.username);
    let [userStatus, setUserStatus] = useState(user.userStatus);
    let [email, setEmail] = useState(user.email);
    let [role, setRole] = useState(user.role);
    let [controllerEmail, setControllerEmail] = useState(user.assignedController);
    let [approverEmail, setApproverEmail] = useState(user.assignedApprover);

    let [errorMsg, setErrorMsg] = useState("");
    let [rolesArray, setRolesArray] = useState([]);
    let userStatusArray = ["new", "active", "inactive"];

    useEffect(() => {
        Axios.get(backendPort + "roles")
        .then(res => {
            setRolesArray(res.data)
        })
    }, [backendPort])

    const saveBtnHandler = () => {
        if (
            username.trim() === "" ||
            email === "" ||
            role === "" ||
            userStatus === ""
        ) {
            setErrorMsg("Please complete all necessary fields.")
        }
        else {
            let editedUser = {
                _id: user._id,
                username,
                email,
                role,
                assignedController: controllerEmail,
                assignedApprover: approverEmail,
                userStatus
            }
            Axios.put(backendPort + "users/" + editedUser._id, editedUser, {headers: {authorization: currentUser.access}})
            .then((res) => {
                if (res.data === "invalid") {
                    setErrorMsg("Username is already existing.")
                }
                else {
                    setUserForEdit({})
                    setErrorMsg("");
                    setSubPage("List");
                    let userListCopy = [...userList];
                    let index = userListCopy.findIndex(user => user._id === res.data._id);
                    userListCopy[index] = res.data;
                    setUserList(userListCopy);
                }
            })
        }
    };
    const cancelBtnHandler = () => {
        setUserForEdit({});
        setSubPage("List")
    };

    return(
        <div className="user-edit">
            <div>
                <div>Username</div>
                <input
                    type = "text"
                    value = {username}
                    onChange = {e => setUsername(e.target.value)}
                />
            </div>
            <div>
                <div>Status</div>
                <select
                    value = {userStatus}
                    onChange = {e => setUserStatus(e.target.value)}
                >{userStatusArray.map(status => <option key={status}>{status}</option>)}
                </select>
            </div>
            <div>
                <div>Role</div>
                <select
                    value = {role}
                    onChange = {e => setRole(e.target.value)}
                >{rolesArray.map(role => <option key={role}>{role}</option>)}
                </select>
            </div>
            <div>
                <div>Email Address</div>
                <input
                    type = "email"
                    value = {email}
                    onChange = {e => setEmail(e.target.value)}
                />
            </div>
            {
                role === "proponent" ?
                <>
                    <div>
                        <div>Assigned Controller</div>
                        <input
                            type = "email"
                            value = {controllerEmail}
                            onChange = {e => setControllerEmail(e.target.value)}
                        />
                    </div>
                    <div>
                        <div>Assigned Approver</div>
                        <input
                            type = "email"
                            value = {approverEmail}
                            onChange = {e => setApproverEmail(e.target.value)}
                        />
                    </div>
                </>
                : null
            }
            <div>
                {errorMsg}
            </div>
            <div className="btn-div">
                <button onClick={saveBtnHandler}>Save</button>
                <button onClick={cancelBtnHandler}>Cancel</button>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        backendPort: state.navSlice.backendPort,
        currentUser: state.loginSlice.currentUser
    }
};

export default connect(mapStateToProps)(UserEdit);