import { connect } from "react-redux";
import EventRow from "./EventRow";

const EventsSection = (props) => {
    return (
        <div id="events-section">
            <div className="claim-section-header">
                <h3>Claim Events</h3>
            </div>
            <div className="event-row event-row-header">
                <div>Event</div>
                <div>User</div>
                <div>Timestamp</div>
            </div>
            <div className="events-rows-section">
                {props.claimForViewing.events.map(event => 
                    <EventRow event={event} key={event._id}/>
                )}
            </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        claimForViewing: state.viewClaimsSlice.claimForViewing,
    }
};

export default connect(mapStateToProps)(EventsSection);