import { connect } from "react-redux";
import Axios from "axios";
import { formatDate } from "../../Global";
import imgView from "../../../../assets/imgs/view.png";
import imgPullOut from "../../../../assets/imgs/remove.png";

const ClaimRow = ({
    backendPort,
    currentUser,
    claim,
    setModalDisp,
    setClaimForPullOut,
    viewClaimDetails,
    activatePage,
    setUserMode
}) => {

    let formattedDate = formatDate(claim.updatedAt);
    let enablePullOutBtn = false;
    if (currentUser.role === "admin" ||
        (currentUser.role === "proponent" && claim.currentStep === "returned to proponent")
    ) {enablePullOutBtn = true}

    const viewBtnHandler = () => {
        Axios.get(backendPort + "claims/" + claim._id, {headers: {authorization: currentUser.access}})
        .then(res => {
            viewClaimDetails(res.data);
            activatePage("ClaimDetails");
            setUserMode("view");
        })
    };
    const pullOutBtnHandler = () => {
        setModalDisp({
            display: true,
            msg: "Are you sure you want to pull out this claim?",
            msgDetails:
                `Claim Id: ${claim._id},
                Amount: Php ${claim.amount}`,
            modalAction: "confirmPullOut"
        })
        setClaimForPullOut(claim)
    };

    return(
        <div className="view-claims-row">
                <div>
                    <div>Claim ID</div>
                    <div>{claim._id}</div>
                </div>
                <div>
                    <div>Purpose</div>
                    <div>{claim.claimDesc}</div>
                </div>
                <div>
                    <div>Status</div>
                    <div>{claim.currentStep}</div>
                </div>
                <div>
                    <div>Proponent</div>
                    <div>{claim.proponentEmail}</div>
                </div>
                <div>
                    <div>Amount</div>
                    <div>{claim.amount.toLocaleString('en', {useGrouping:true})}</div>
                </div>
                <div>
                    <div>Date Modified</div>
                    <div>{formattedDate}</div>
                </div>
                <div className="btn-div">
                    <button onClick={viewBtnHandler}>
                        <img 
                            src = {imgView}
                            alt = "view"
                        />
                    </button>
                    {currentUser.role === "proponent" || currentUser.role === "admin" ?
                        <button onClick={pullOutBtnHandler} disabled={!enablePullOutBtn}>
                            <img 
                                src = {imgPullOut}
                                alt = "remove"
                            />
                        </button>
                        : null
                    }
                </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        months: state.navSlice.months,
        currentUser: state.loginSlice.currentUser,
        backendPort: state.navSlice.backendPort,
        userMode:state.navSlice.userMode
    }
};
const mapDispatchToProps = (dispatch) => {
    return {
        viewClaimDetails: (claim) => dispatch({
            type: "VIEW_CLAIM_DETAILS",
            payload: claim
        }),
        activatePage: (page) => dispatch({
            type: "ACTIVATE_PAGE",
            payload: page
        }),
        setUserMode: (mode) => dispatch({
            type: "SET_USER_MODE",
            payload: mode
        })
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(ClaimRow);