import {connect} from "react-redux";
import { formatDate } from "../../Global";
import imgEdit from "../../../../assets/imgs/edit.png";
import imgRemove from "../../../../assets/imgs/remove.png";

const UserRow = ({
    user,
    setUserForEdit,
    setUserForDelete,
    setSubPage,
    setModalDisp,
    setModalMsg,
}) => {

    let formattedDate = formatDate(user.updatedAt);
    
    const editBtnHandler = () => {
        setUserForEdit(user);
        setSubPage("Edit");
    };
    const removeBtnHandler = () => {
        setUserForDelete(user);
        setModalDisp(true);
        setModalMsg("Are you sure you want to delete this user?");
    };

    return(
        <div className="user-row">
            <div>
                <div>Username</div>
                <div>{user.username}</div>
            </div>
            <div>
                <div>Status</div>
                <div>{user.userStatus}</div>
            </div>
            <div>
                <div>Role</div>
                <div>{user.role}</div>
            </div>
            <div>
                <div>Email</div>
                <div>{user.email}</div>
            </div>
            <div>
                <div>Controller</div>
                <div>{user.assignedController}</div>
            </div>
            <div>
                <div>Approver</div>
                <div>{user.assignedApprover}</div>
            </div>
            <div>
                <div>Date Modified</div>
                <div>{formattedDate}</div>
            </div>
            <div className="btn-div">
                <button onClick={editBtnHandler}>
                    <img 
                        src = {imgEdit}
                        alt = "edit"
                    />
                </button>
                <button onClick={removeBtnHandler}>
                    <img 
                        src = {imgRemove}
                        alt = "remove"
                    />
                </button>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        backendPort: state.navSlice.backendPort,
        months: state.navSlice.months,
        currentUser: state.loginSlice.currentUser,
    }
}

export default connect(mapStateToProps)(UserRow);