import "../assets/refund-app-mobile.css"
import "../assets/refund-app-tablet.css"
import "../assets/refund-app-large.css"
import Banner from "./subcomponents/Banner";
import ClaimDetails from "./subcomponents/body/claim-details/ClaimDetails";
import ClaimList from "./subcomponents/body/claim-list/ClaimList";
import Dashboard from "./subcomponents/body/Dashboard";
import UserMgt from "./subcomponents/body/user-mgt/UserMgt";
import LandingPage from "./subcomponents/landing-page/LandingPage";
import Navigation from "./subcomponents/Navigation";
import {connect} from "react-redux";

const RefundApp = ({activePage, currentUser}) => {
    return(
        <div id="refund-app">
                {
                    activePage === "LandingPage" &&
                    currentUser.userStatus !== "active" ?
                    <LandingPage /> : null
                }
                {
                    currentUser.userStatus === "active" ?
                    <div id="main-window">
                        <div className="banner-nav">
                            <Banner />
                            <Navigation />
                        </div>
                        <div>
                            {activePage === "Dashboard" ? <Dashboard /> : null}
                            {activePage === "ViewClaims" ? <ClaimList /> : null}
                            {activePage === "ClaimDetails" ? <ClaimDetails /> : null}
                            {activePage === "UserMgt" && currentUser.role === "admin" ? <UserMgt /> : null}
                        </div>
                    </div>
                    : null
                }

        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        currentUser: state.loginSlice.currentUser,
        activePage: state.navSlice.activePage
    }
};

export default connect(mapStateToProps)(RefundApp);