let savedUsername = localStorage.getItem("username");
let savedRole = localStorage.getItem("role");
let savedAccess = localStorage.getItem("access");
let savedEmail = localStorage.getItem("email");
let savedAssignedController = localStorage.getItem("assignedController");
let savedAssignedApprover = localStorage.getItem("assignedApprover");
let savedUserStatus = localStorage.getItem("userStatus");

const initialState = {
    currentUser:
        savedUsername === null ?
        {
            username: "",
            role: "",
            access: "",
            email: "",
            assignedController: "",
            assignedApprover: "",
            userStatus: ""
        }
        :
        {
            username: savedUsername,
            role: savedRole,
            access: savedAccess,
            email: savedEmail,
            assignedController: savedAssignedController,
            assignedApprover: savedAssignedApprover,
            userStatus: savedUserStatus
        }
}

const loginSlice = (state = initialState, action) => {
    switch(action.type) {
        case "SAVE_USER_INFO":
            localStorage.setItem("username", action.payload.username);
            localStorage.setItem("role", action.payload.role);
            localStorage.setItem("access", action.payload.access);
            localStorage.setItem("email", action.payload.email);
            localStorage.setItem("assignedController", action.payload.assignedController);
            localStorage.setItem("assignedApprover", action.payload.assignedApprover);
            localStorage.setItem("userStatus", action.payload.userStatus);
            return {
                ...state,
                currentUser: action.payload
            };
        default: return state;
    }
}

export default loginSlice;

