const initialState = {
    claimsList: [],
    claimForViewing: ""
}

const viewClaimsSlice = (state = initialState, action) => {
    switch (action.type) {
        case "SHOW_CLAIMS":
            return {
                ...state,
                claimsList: action.payload
            };
        case "VIEW_CLAIM_DETAILS":
            let claimCopy = action.payload;
            if (claimCopy !== "") {
                claimCopy.receipts.forEach(receipt => {
                    let receiptDateCopy = new Date(receipt.receiptDate)
                    let year = receiptDateCopy.getFullYear();
                    let month = receiptDateCopy.getMonth() + 1
                    if (month < 10) {
                        month = "0" + month
                    }
                    let date = receiptDateCopy.getDate();
                    if (date < 10) {
                        date = "0" + date
                    }
                    receipt.receiptDate = `${year}-${month}-${date}`
                })
            }
            return {
                ...state,
                claimForViewing: claimCopy
            };
        default: return state;
    }
}

export default viewClaimsSlice;