import {useEffect, useState} from "react";
import { connect } from "react-redux";
import ReceiptSection from "./ReceiptSection";
import EventsSection from "./EventsSection";
import Axios from "axios";
import imgEdit from "../../../../assets/imgs/edit.png";
import { v4 as uuidv4 } from 'uuid';
import Plus from "../../../../assets/imgs/plus.png";
import imgBack from "../../../../assets/imgs/previous.png";
import imgSubmit from "../../../../assets/imgs/submit.png";


const ClaimDetails = ({
    appName,
    backendPort,
    currentUser,
    consolidateReceipts,
    addClaimDesc,
    activatePage,
    resetClaim,
    claimDesc,
    receipts,
    receiptsTotalAmt,
    claimForViewing,
    viewClaimDetails,
    userMode,
    setUserMode,
    addReceipt,
}) => {

    let [description, setDescription] = useState("");
    let [finDoc, setFinDoc] = useState("");
    let [statusSelect, setStatusSelect] = useState("");
    let [statusOptions, setStatusOptions] = useState([]);
    let [modalDisp, setModalDisp] = useState({
        display: false,
        msg: "",
        msgDetails1: "",
        msgDetails2: "",
        modalAction: ""
    });

    // variables for disabling fields
    let enableInputFields;
    let enableFinDocField;
    let enableStatusSelector;
    let showClaimSelectDiv;
    if (currentUser.role === "proponent" && userMode === "view") {
        enableInputFields = false;
        enableFinDocField = false;
        enableStatusSelector = false;
    } else if (currentUser.role === "proponent" && userMode !== "view") {
        enableInputFields = true;
        enableFinDocField = false;
        enableStatusSelector = true;
    } else if (currentUser.role === "controller" && userMode === "view") {
        enableInputFields = false;
        enableFinDocField = true;
        enableStatusSelector = true;
    } else { // approver view
        enableInputFields = false;
        enableFinDocField = false;
        enableStatusSelector = true;
    };
    if (userMode === "create") {
        showClaimSelectDiv = false;
    }
    else {
        if (currentUser.role === "proponent" && userMode === "view") {
            showClaimSelectDiv = false;
        }
        else {
            showClaimSelectDiv = true;
        };
    };

    // Getting status options
    useEffect(() => {
        Axios.get(backendPort + "statusoptions", {headers: {authorization: currentUser.access}})
        .then(res => {
            if (res.data !== "invalid") {
                setStatusOptions(res.data)
            }
        })
    }, [backendPort, currentUser.access]);

    // If detailed view of claims, placing of previous entry in the input boxes
    useEffect(() => {
        if (claimForViewing === "") {
            setDescription("")
            setFinDoc("")
            setStatusSelect("")
        }
        else {
            setDescription(claimForViewing.claimDesc)
            setFinDoc(claimForViewing.finDocNum)
            setStatusSelect(statusOptions[0])
        }
    }, [claimForViewing, statusOptions])

    // adding new receipt row
    const addRcptBtnHandler = () => {
        let newReceipt = {
            receiptCount: uuidv4()
        }
        addReceipt(newReceipt);
    }

    // change mode to edit
    const editBtnHandler = () => {
        setUserMode("edit")
    };

    // close the modal
    const backBtnHandler = () => {
        consolidateReceipts(false);
        setModalDisp({
            display: false,
            msg: "",
            msgDetails1: "",
            msgDetails2: "",
            modalAction: ""
        })
    };

    // confirming new claim submission
    const submitBtnHandler = () => {
        addClaimDesc(description);
        consolidateReceipts(true);
        setModalDisp({
            display: true,
            msg: "Are you sure you want to submit this claim?",
            msgDetails1: "",
            msgDetails2: "",
            modalAction: "confirmSubmission"
        })
    };

    // change claim status confirmation
    const setClaimStatusHandler = () => {
        addClaimDesc(description);
        consolidateReceipts(true);
        setModalDisp({
            display: true,
            msg: "Are you sure you want to change the status of this claim?",
            msgDetails1: `Previous status: ${claimForViewing.currentStep}`,
            msgDetails2: `Proposed action: ${statusSelect}`,
            modalAction: "confirmEditSubmission"
        })
    }

    // fxn for vldn of receipts
    const receiptsVldnFxn = (receipts) => {
        let receiptsVldn = true;
        if (receipts.length === 0) {
            receiptsVldn = false;
        }
        receipts.forEach(receipt => {
            if (
                receipt.receiptNum === "" ||
                receipt.receiptDate === "" ||
                receipt.receiptDetails === "" ||
                receipt.expenseType === "" ||
                receipt.receiptAmount === 0 ||
                receipt.receiptNum === undefined ||
                receipt.receiptDate === undefined ||
                receipt.receiptDetails === undefined ||
                receipt.expenseType === undefined ||
                receipt.receiptAmount === "" ||
                receipt.receiptAmount === undefined
            ) {
                receiptsVldn = false;
            }
        })
        return receiptsVldn
    }

    // fxn for submitting of edited claim
    const backendEditSubmission = (newClaim) => {
        Axios.put(backendPort + "claims/edit/" + claimForViewing._id, newClaim, {headers: {authorization: currentUser.access}})
        .then(res => {
            if (res.data !== "invalid") {
                setModalDisp({
                    display: true,
                    msg: "Done.",
                    msgDetails1: `RefNum: ${res.data._id}`,
                    msgDetails2: `Total Amount: P${res.data.amount}`,
                    modalAction: "submissionSuccess"
                })
            }
            else {
                setModalDisp({
                    display: true,
                    msg: "Unsuccessful.",
                    msgDetails1: "",
                    msgDetails2: "",
                    modalAction: "submissionFailed"
                })
            }
            consolidateReceipts(false);
        })
    }

    // modal: proceed with submission of new claim
    const proceedBtnHandler = () => {
        let vldn = receiptsVldnFxn(receipts);
        if (vldn === false) {
            consolidateReceipts(false);
            setModalDisp({
                display: true,
                msg: "Invalid input. Check all receipts",
                msgDetails1: "",
                msgDetails2: "",
                modalAction: "stopSubmission"
            })
        }
        else {
            let newClaim = {
                claimDesc,
                receiptsArray: receipts,
                amount: receiptsTotalAmt
            }
            Axios.post(backendPort + "claims", newClaim, {headers: {authorization: currentUser.access}})
            .then(res => {
                if (res.data !== "invalid") {
                    setModalDisp({
                        display: true,
                        msg: "Claim submitted.",
                        msgDetails1: `RefNum: ${res.data._id}`,
                        msgDetails2: "",
                        modalAction: "submissionSuccess"
                    })
                }
                else {
                    setModalDisp({
                        display: true,
                        msg: "Unsuccessful.",
                        msgDetails1: "",
                        msgDetails2: "",
                        modalAction: "submissionFailed"
                    })
                }
                consolidateReceipts(false);
            })
        }
    };

    // modal: proceed with saving the edit
    const proceedEditBtnHandler = () => {
        let vldn = receiptsVldnFxn(receipts);
        if (!vldn) {
            consolidateReceipts(false);
            setModalDisp({
                display: true,
                msg: "Invalid input. Check all receipts",
                msgDetails1: "",
                msgDetails2: "",
                modalAction: "stopSubmission"
            })
        }
        else {
            let newClaim = {
                claimDesc,
                receiptsArray: receipts,
                amount: receiptsTotalAmt,
                currentStep: statusSelect
            };
            if (currentUser.role === "controller") {
                if (finDoc === 0) {
                    setModalDisp({
                        display: true,
                        msg: "No financial document number.",
                        msgDetails1: "",
                        msgDetails2: "",
                        modalAction: "stopSubmission"
                    })
                }
                else {
                    newClaim.finDocNum = finDoc;
                    backendEditSubmission(newClaim);
                }
            } else {
                backendEditSubmission(newClaim);
            }
        }
    }

    // back to view claims
    const cancelBtnHandler = () => {
        consolidateReceipts(false);
        setDescription("");
        setFinDoc("");
        setModalDisp({
            display: false,
            msg: "",
            msgDetails1: "",
            msgDetails2: "",
            modalAction: ""
        })
        activatePage("ViewClaims");
        resetClaim();
        viewClaimDetails("");
    };

    return(

        <div id="claim-details-div">
            {userMode === "create" ? <h2>Create New Claim</h2> : null}
            {userMode === "edit" ? <h2>Edit Claim</h2> : null}
            {userMode === "view" ? <h2>View Claim Details</h2> : null}

            <div className="input-div">
                <div>Purpose</div>
                <input 
                    type = "text"
                    value = {description}
                    onChange = {e => setDescription(e.target.value)}
                    disabled = {!enableInputFields}
                />
            </div>
                
            <ReceiptSection enableInputFields={enableInputFields}/>

            {userMode !== "create" ? 
                <>
                    <EventsSection /> 
                    <div id="fin-doc-section" className={currentUser.role === "proponent" && userMode === "view" ? "adj-fin-doc-section" : ""}>
                        <div className="claim-section-header">
                            <h3>Financial Doc Number</h3>
                        </div>
                        <div>
                            <input 
                                type = "number"
                                value = {finDoc}
                                onChange = {(e) => setFinDoc(e.target.value)}
                                disabled = {!enableFinDocField}
                            />
                        </div>
                    </div>
                </>
                : null
            }

            {showClaimSelectDiv ?
                <div id="change-claim-status">
                    <div className="claim-section-header">
                        <h3>Change Claim Status</h3>
                    </div>
                    <div>
                        <select
                            value = {statusSelect === "" ? statusOptions[0] : statusSelect}
                            disabled = {!enableStatusSelector}
                            onChange = {(e) => setStatusSelect(e.target.value)}    
                        >
                            {
                                statusOptions.map(option => <option key={option}>{option}</option>)
                            }
                        </select>
                        <button
                            disabled = {!enableStatusSelector}
                            onClick = {setClaimStatusHandler}
                        >Submit</button>
                    </div>
                </div>
                : null
            }

            <div className="claim-details-btn-div">
                <button onClick={cancelBtnHandler}>
                    <img
                        src = {imgBack}
                        alt = "back"
                    />
                    <p>Back</p>
                </button>
                {
                    userMode === "create" ? 
                    <button onClick={submitBtnHandler}>
                        <img
                            src = {imgSubmit}
                            alt = "submit"
                        />
                        <p>Submit</p>
                    </button>
                    : null
                }
                {
                    userMode === "view" ? null :
                    <button onClick={addRcptBtnHandler} className="add-receipt-btn">
                        <img
                            src = {Plus}
                            alt = "add receipt"
                        />
                        <p>Receipt</p>
                    </button>
                }
                {
                    userMode === "view" &&
                    currentUser.role === "proponent" &&
                    claimForViewing.currentStepUserEmail === currentUser.email ? 
                    <button onClick={editBtnHandler}  className="edit-btn">
                        <img 
                            src = {imgEdit}
                            alt = "edit"
                        />
                        <p>Edit</p>
                    </button>
                    : null
                }                
            </div>

            {
                modalDisp.display ?
                <div className="modal-container claim-details-modal-cont">
                    <div className="modal">
                        <div>{appName}</div>
                        <div className="modalbody">
                            <div>
                                <div>{modalDisp.msg}</div>
                                <div>{modalDisp.msgDetails1}</div>
                                {modalDisp.msgDetails2 ? <div>{modalDisp.msgDetails2}</div> : null}
                            </div>
                            <div className="modal-btn-div">
                                {modalDisp.modalAction === "confirmSubmission" ?
                                    <>
                                        <button onClick={proceedBtnHandler}>Proceed</button>
                                        <button onClick={backBtnHandler}>Back</button>
                                    </>
                                    : null
                                }
                                {modalDisp.modalAction === "submissionSuccess" || modalDisp.modalAction === "submissionFailed"?
                                    <button onClick={cancelBtnHandler}>OK</button>
                                    :
                                    null
                                }
                                {modalDisp.modalAction === "confirmEditSubmission" ?
                                    <>
                                        <button onClick={proceedEditBtnHandler}>Proceed</button>
                                        <button onClick={backBtnHandler}>Back</button>
                                    </>
                                    : null
                                }
                                {modalDisp.modalAction === "stopSubmission" ?
                                    <>
                                        <button onClick={backBtnHandler}>Back</button>
                                    </>
                                    : null
                                }
                            </div>
                        </div>
                    </div>
                </div>
                : null
            }
        </div>
    )
};

const mapStateToProps = (state) => {
    return {
        appName: state.navSlice.appName,
        backendPort: state.navSlice.backendPort,
        currentUser: state.loginSlice.currentUser,
        receipts: state.claimsSlice.receipts,
        claimDesc: state.claimsSlice.claimDesc,
        receiptsTotalAmt: state.claimsSlice.receiptsTotalAmt,
        claimForViewing: state.viewClaimsSlice.claimForViewing,
        userMode:state.navSlice.userMode
    }
};
const mapDispatchToProps = (dispatch) => {
    return {
        activatePage: (page) => dispatch({
            type: "ACTIVATE_PAGE",
            payload: page
        }),
        addReceipt: (receipt) => dispatch({
            type: "ADD_RECEIPT",
            payload: receipt
        }),
        consolidateReceipts: (boolean) => dispatch({
            type: "CONSOLIDATE_RECEIPTS",
            payload: boolean
        }),
        addClaimDesc: (desc) => dispatch({
            type: "ADD_CLAIM_DESC",
            payload: desc
        }),
        resetClaim: () => dispatch({
            type: "RESET_CLAIM"
        }),
        viewClaimDetails: (claim) => dispatch({
            type: "VIEW_CLAIM_DETAILS",
            payload: claim
        }),
        setUserMode: (mode) => dispatch({
            type: "SET_USER_MODE",
            payload: mode
        })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClaimDetails);