import {useState, useEffect} from "react";
import UserEdit from "./UserEdit";
import UserRow from "./UserRow";
import Axios from "axios";
import { connect } from "react-redux";

const UserMgt = ({backendPort, currentUser, appName}) => {
    let [userList, setUserList] = useState([]);
    let [subPage, setSubPage] = useState("List");
    let [userForEdit, setUserForEdit] = useState({});
    let [userForDelete, setUserForDelete] = useState({});
    let [deletionSuccessful, setDeletionSuccessful] = useState(false);
    let [modalDisp, setModalDisp] = useState(false);
    let [modalMsg, setModalMsg] = useState("");

    useEffect(() => {
        Axios.get(backendPort + "users", {headers: {authorization: currentUser.access}})
        .then(res => setUserList(res.data))
    }, [backendPort, currentUser.access])

    const deleteBtnHandler = () => {
        Axios.delete(backendPort + "users/" + userForDelete._id, {headers: {authorization: currentUser.access}})
        .then(res => {
            setDeletionSuccessful(true)
            setModalMsg("User has been deleted.");
            setUserForDelete(res.data);
            let userListCopy = [...userList];
            let index = userListCopy.findIndex(user => user._id === res.data._id);
            userListCopy.splice(index, 1);
            setUserList(userListCopy);
        })
    }
    const cancelBtnHandler = () => {
        setUserForDelete({});
        setModalMsg("");
        setDeletionSuccessful(false);
        setModalDisp(false);
    };

    return(
        <div id="user-mgt-section">
            <h2>User Mgt</h2>
            {
                subPage === "List" ?
                <div>
                    <div className="user-row user-row-header">
                        <div>Username</div>
                        <div>User Status</div>
                        <div>Role</div>
                        <div>Email</div>
                        <div>Assigned Controller</div>
                        <div>Assigned Approver</div>
                        <div>Date Modified</div>
                        <div></div>
                    </div>
                    <div className="user-rows-div">
                    {
                        userList.map(user =>
                            <UserRow
                                key = {user._id}
                                user = {user}
                                setSubPage = {setSubPage}
                                setUserForEdit = {setUserForEdit}
                                setUserForDelete = {setUserForDelete}
                                setModalDisp = {setModalDisp}
                                setModalMsg = {setModalMsg}
                            />
                        )
                    }
                    </div>
                </div>
                :
                <UserEdit 
                    user = {userForEdit}
                    setSubPage = {setSubPage}
                    setUserForEdit = {setUserForEdit}
                    userList = {userList}
                    setUserList = {setUserList}
                />
            }
            {
                modalDisp ?
                <div className="modal-container">
                    <div className="modal">
                        <div>{appName}</div>
                        <div className="modalbody">
                            <div>
                                <div>{modalMsg}</div>
                                <div>{userForDelete.username}</div>
                            </div>
                            {
                                deletionSuccessful ?
                                <div className="modal-btn-div">
                                    <button onClick={cancelBtnHandler}>OK</button>
                                </div>
                                :
                                <div className="modal-btn-div">
                                    <button onClick={deleteBtnHandler}>Delete</button>
                                    <button onClick={cancelBtnHandler}>Cancel</button>
                                </div>
                            }
                        </div>
                    </div>
                </div>
                : null
            }
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        appName: state.navSlice.appName,
        backendPort: state.navSlice.backendPort,
        currentUser: state.loginSlice.currentUser
    }
};

export default connect(mapStateToProps)(UserMgt);