import {useState, useEffect} from "react";
import Axios from "axios";
import ReceiptRow from "./ReceiptRow";
import {connect} from "react-redux";

const ReceiptSection = ({
    backendPort,
    receipts,
    receiptsTotalAmt,
    userMode,
    claimForViewing,
    setReceiptsArray,
    enableInputFields,
}) => {

    let [expenseTypes, setExpenseTypes] = useState([])

    // Get options for expense types
    useEffect(()=>{
        Axios.get(backendPort + "expensetypes")
        .then(res => setExpenseTypes(res.data))
    }, [backendPort])

    // View mode: render receipts of selected claim
    useEffect(() => {
        if(userMode === "view") {
            setReceiptsArray(claimForViewing.receipts, claimForViewing.amount)
        };
    }, [receipts, claimForViewing, setReceiptsArray, userMode])

    return(
        <div id="receipts-section">
            <div>
                <div className="claim-section-header">
                    <h3>Receipts</h3>
                </div>
            </div>
            <div className="receipt-row receipt-row-header">
                <div>Receipt #</div>
                <div>Date</div>
                <div className={!enableInputFields ? "adj-details" : null}>Details</div>
                <div>Expense Type</div>
                <div>Amount</div>
                {enableInputFields ? <div></div> : null}
            </div>
            <div className="receipt-rows-section">
                {
                    receipts.length === 0 ?
                    <div className="no-receipts">No receipts submitted.</div>
                    :
                    receipts.map(receipt => 
                    <ReceiptRow
                        key = {receipt.receiptCount}
                        receipt = {receipt}
                        expenseTypes = {expenseTypes}
                        enableInputFields = {enableInputFields}
                    />
                )}
            </div>
            <div className="total-amt">
                <div>
                    Total Amount
                </div>
                <div>Php {receiptsTotalAmt.toLocaleString('en', {useGrouping:true})}</div>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        backendPort: state.navSlice.backendPort,
        currentUser: state.loginSlice.currentUser,
        receipts: state.claimsSlice.receipts,
        receiptsTotalAmt: state.claimsSlice.receiptsTotalAmt,
        claimForViewing: state.viewClaimsSlice.claimForViewing,
        userMode:state.navSlice.userMode
    }
};
const mapDispatchToProps = (dispatch) => {
    return {
        setReceiptsArray: (receiptsArray, receiptsTotalAmt) => dispatch({
            type: "SET_RECEIPTS_ARRAY",
            payload: {receiptsArray, receiptsTotalAmt}
        })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ReceiptSection);