import {useState} from "react";
import {connect} from "react-redux";
import Axios from "axios";

const Login = (props) => {
    let [username, setUsername] = useState("");
    // let [password, setPassword] = useState("");
    let [errorMsg, setErrorMsg] = useState("");
    let [modalDisp, setModalDisp] = useState(false);

    const loginRoleHandler = (role) => {
        setUsername(role);
        let userVldn = {username: role, password: "123"};
        Axios.post(props.backendPort + "login", userVldn)
        .then(res => {
            if(res.data === "invalid") {
                setErrorMsg("Invalid credentials.")
            }
            else {
                if (res.data.userStatus !== "active") {
                    setModalDisp(true)
                }
                else {
                    let loggedUser = {
                        username: role,
                        role: res.data.role,
                        access: res.data.access,
                        email: res.data.email,
                        assignedController: res.data.assignedController,
                        assignedApprover: res.data.assignedApprover,
                        userStatus: res.data.userStatus
                    }
                    props.saveUserInfo(loggedUser);
                    props.activatePage("ViewClaims");
                }
            }
        })
    }

    // const loginBtnHandler = () => {
    //     if (username.trim() === "" || password === "") {
    //         setErrorMsg("Please complete all fields.")
    //     }
    //     else {
    //         let userVldn = {username, password}
    //         Axios.post(props.backendPort + "login", userVldn)
    //         .then(res => {
    //             if(res.data === "invalid") {
    //                 setErrorMsg("Invalid credentials.")
    //             }
    //             else {
    //                 if (res.data.userStatus !== "active") {
    //                     setModalDisp(true)
    //                 }
    //                 else {
    //                     let loggedUser = {
    //                         username,
    //                         role: res.data.role,
    //                         access: res.data.access,
    //                         email: res.data.email,
    //                         assignedController: res.data.assignedController,
    //                         assignedApprover: res.data.assignedApprover,
    //                         userStatus: res.data.userStatus
    //                     }
    //                     props.saveUserInfo(loggedUser);
    //                     props.activatePage("ViewClaims");
    //                 }
    //             }
    //         })
    //     }
    // }
    const okBtnHandler = () => {
        setModalDisp(false);
        setUsername("");
        // setPassword("");
        setErrorMsg("");
    }

    return(
        <>
            {!modalDisp ?
                <div className="landing-page-div login">
                    <h2>Login</h2>
                    <div className="demo-roles">
                        <h3>For demo purposes, pick role:</h3>
                        <button onClick={() => loginRoleHandler("proponent1")}>Proponent</button>
                        <button onClick={() => loginRoleHandler("controller1")}>Controller</button>
                        <button onClick={() => loginRoleHandler("approver1")}>Approver</button>
                        <button onClick={() => loginRoleHandler("admin")}>Admin</button>
                        {errorMsg ? <div className="error-msg">{errorMsg}</div> : null}
                    </div>
                    {/* <div>
                        <div>Username</div>
                        <input 
                            type = "text"
                            value = {username}
                            onChange = {e => setUsername(e.target.value)}
                        />
                    </div>
                    <div>
                        <div>Password</div>
                        <input 
                            type = "password"
                            value = {password}
                            onChange = {e => setPassword(e.target.value)}
                        />
                    </div>
                    {errorMsg ? <div className="error-msg">{errorMsg}</div> : null}
                    <div className="button-div login-user">
                        <button onClick={loginBtnHandler}>Login</button>
                    </div>
                    <div className="button-div new-user">
                        <div>New user? <br/>Let's create an account!</div>
                        <button onClick={() => props.setSubPage("Register")}>Register</button>
                    </div> */}
                </div>
                :
                <div className="modal-parent-cont">
                    <div className="modal-container">
                        <div className="modal modal-landingpg">
                            <div>{props.appName}</div>
                            <div className="modalbody">
                                <div>
                                    <div>Admin has not changed the status of your account ({username}) yet.</div>
                                </div>
                                <div className="modal-btn-div">
                                    <button onClick={okBtnHandler}>OK</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}

const mapStateToProps = (state) => {
    return {
        appName: state.navSlice.appName,
        backendPort: state.navSlice.backendPort
    }
};
const mapDispatchToProps = (dispatch) => {
    return {
        saveUserInfo: (loggedUser) => dispatch({
            type: "SAVE_USER_INFO",
            payload: loggedUser
        }),
        activatePage: (page) => dispatch({
            type: "ACTIVATE_PAGE",
            payload: page
        })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login);