import Lodash from "lodash";

const initialState = {
    claimDesc: "",
    receipts: [],
    receiptsTotalAmt: 0,
    consolidateReceipts: false
}

const claimsSlice = (state = initialState, action) => {
    let receiptsCopy = Lodash.cloneDeep(state.receipts);
    let newReceiptsTotalAmt = 0;
    let index;
    switch (action.type) {
        case "SET_RECEIPTS_ARRAY":
            return {
                ...state,
                receipts: action.payload.receiptsArray,
                receiptsTotalAmt: action.payload.receiptsTotalAmt
            };
        case "RESET_CLAIM":
            return {
                ...state,
                receipts: [],
                receiptsTotalAmt: 0,
            };
        case "ADD_RECEIPT":
            receiptsCopy.push(action.payload);
            return {
                ...state,
                receipts: receiptsCopy
            };
        case "UPDATE_RECEIPT_AMT":
            index = receiptsCopy.findIndex(receipt => receipt.receiptCount === action.payload.receiptCount);
            if (receiptsCopy[index].receiptAmount === undefined) {
                newReceiptsTotalAmt = state.receiptsTotalAmt + action.payload.amt;
            }
            else {
                newReceiptsTotalAmt = state.receiptsTotalAmt + (action.payload.amt - receiptsCopy[index].receiptAmount);
            };
            receiptsCopy[index].receiptAmount = action.payload.amt;
            return {
                ...state,
                receipts: receiptsCopy,
                receiptsTotalAmt: newReceiptsTotalAmt
            };
        case "CONSOLIDATE_RECEIPTS":
            return {
                ...state,
                consolidateReceipts: action.payload
            };
        case "SUBMIT_RECEIPT_DATA":
            index = receiptsCopy.findIndex(receipt => receipt.receiptCount === action.payload.receiptCount);
            receiptsCopy[index] = action.payload;
            let finalItem = false;
            index === receiptsCopy.length - 1 ? finalItem = true : finalItem = false;
            return {
                ...state,
                receipts: receiptsCopy,
                consolidateReceipts: finalItem
            };
        case "DELETE_RECEIPT":
            index = receiptsCopy.findIndex(receipt => receipt.receiptCount === action.payload.receiptCount);
            receiptsCopy.splice(index, 1)
            if (action.payload.receiptAmount !== undefined) {
                newReceiptsTotalAmt = state.receiptsTotalAmt - action.payload.receiptAmount
            }
            else {
                newReceiptsTotalAmt = state.receiptsTotalAmt
            }
            return {
                ...state,
                receipts: receiptsCopy,
                receiptsTotalAmt: newReceiptsTotalAmt
            };
        case "ADD_CLAIM_DESC":
            return {
                ...state,
                claimDesc: action.payload
            }
        default: return state;
    }
}

export default claimsSlice;