import {useState, useEffect} from "react";
import {connect} from "react-redux";
import Axios from "axios";

const Registration = ({backendPort, setSubPage, appName}) => {
    let [username, setUsername] = useState("");
    let [email, setEmail] = useState("");
    let [password, setPassword] = useState("");
    let [confirmedPassword, setConfirmedPassword] = useState("");
    let [role, setRole] = useState("");
    let [controllerEmail, setControllerEmail] = useState("");
    let [approverEmail, setApproverEmail] = useState("");
    let [errorMsg, setErrorMsg] = useState("");
    let [rolesArray, setRolesArray] = useState([]);
    let [modalDisp, setModalDisp] = useState(false);

    useEffect(() => {
        Axios.get(backendPort + "roles")
        .then(res => {
            setRolesArray(res.data)
        })
    }, [backendPort])

    const regBtnHandler = () => {
        if (
            username.trim() === "" ||
            email === "" ||
            password === "" ||
            confirmedPassword === "" ||
            role === ""
        ) {
            setErrorMsg("Please complete all necessary fields.")
        }
        else if (password !== confirmedPassword) {
            setErrorMsg("Password did not match.")
        }
        else {
            let newUser = {
                username,
                password,
                email,
                role,
                assignedController: controllerEmail,
                assignedApprover: approverEmail,
                userStatus: "new"
            }
            Axios.post(backendPort + "register", newUser)
            .then((res) => {
                if (res.data === "invalid") {
                    setErrorMsg("Username is already existing.")
                }
                else {
                    setUsername("");
                    setPassword("");
                    setConfirmedPassword("");
                    setRole("admin");
                    setEmail("");
                    setControllerEmail("");
                    setApproverEmail("");
                    setErrorMsg("");
                    setModalDisp(true);
                }
            })
        }
    }
    const okBtnHandler = () => {
        setModalDisp(false);
        setSubPage("Login");
    }

    return(
        <>
            {!modalDisp ?
                <div className="landing-page-div register">
                    <h2>Register</h2>
                    <div>
                        <div>Username</div>
                        <input 
                            type = "text"
                            value = {username}
                            onChange = {e => setUsername(e.target.value)}
                        />
                    </div>
                    <div>
                        <div>Email Address</div>
                        <input 
                            type = "email"
                            value = {email}
                            onChange = {e => setEmail(e.target.value)}
                        />
                    </div>
                    <div>
                        <div>Password</div>
                        <input 
                            type = "password"
                            value = {password}
                            onChange = {e => setPassword(e.target.value)}
                        />
                    </div>
                    <div>
                        <div>Confirm password</div>
                        <input 
                            type = "password"
                            value = {confirmedPassword}
                            onChange = {e => setConfirmedPassword(e.target.value)}
                        />
                    </div>
                    <div>
                        <div>Role</div>
                        <select
                            value = {role}
                            onChange = {e => setRole(e.target.value)}
                        >
                            <option value="" disabled defaultValue></option>
                            {rolesArray.map(role => <option key={role}>{role}</option>)}
                        </select>
                    </div>
                    {
                        role === "proponent" ?
                        <>
                            <div>
                                <div>Controller Email Address</div>
                                <input 
                                    type = "email"
                                    value = {controllerEmail}
                                    onChange = {e => setControllerEmail(e.target.value)}
                                />
                            </div>
                            <div>
                                <div>Approver Email Address</div>
                                <input 
                                    type = "email"
                                    value = {approverEmail}
                                    onChange = {e => setApproverEmail(e.target.value)}
                                />
                            </div>
                        </>
                        : null
                    }
                    {errorMsg ? <div className="error-msg">{errorMsg}</div> : null}
                    <div className="button-div">
                        <button onClick={regBtnHandler}>Submit to Admin</button>
                        <button onClick={()=>setSubPage("Login")}>Cancel</button>
                    </div>
                </div>
                :
                <div className="modal-parent-cont">
                    <div className="modal-container">
                        <div className="modal modal-landingpg">
                            <div>{appName}</div>
                            <div className="modalbody">
                                <div>
                                    <div>Registered! Please wait for admin approval.</div>
                                </div>
                                <div className="modal-btn-div">
                                    <button onClick={okBtnHandler}>OK</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}

const mapStateToProps = (state) => {
    return {
        appName: state.navSlice.appName,
        backendPort: state.navSlice.backendPort
    }
};
const mapDispatchToProps = (dispatch) => {
    return {
        activatePage: (page) => dispatch({
            type: "ACTIVATE_PAGE",
            payload: page
        })
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Registration);