import {connect} from "react-redux";
// import imgDashboard from "../../assets/imgs/dashboard.png"
import imgView from "../../assets/imgs/view.png"
import imgCreate from "../../assets/imgs/create.png"
import imgUsers from "../../assets/imgs/users.png"
import imgLogout from "../../assets/imgs/logout.png"

const Navigation = (props) => {
    const logoutBtnHandler = () => {
        let loggedUser = {
            username: "",
            role: "",
            access: "",
            assignedController: "",
            assignedApprover: ""
        }
        props.saveUserInfo(loggedUser);
        props.activatePage("LandingPage");
        localStorage.clear();
    }
    const newClaimBtnHandler = () => {
        props.viewClaimDetails("");
        props.setReceiptsArray([], 0);
        props.activatePage("ClaimDetails");
        props.setUserMode("create")
    }
    return(
        <nav>
            <div>
                <div>
                    <h3>Hello, {props.currentUser.username}!</h3>
                </div>
                <div className="btn-div">
                    {/* <div>
                        <button onClick={() => props.activatePage("Dashboard")}>
                            <img 
                                src = {imgDashboard}
                                alt = "dashboard"
                            />
                        <p>Dashboard</p>
                        </button>
                    </div> */}
                    <div>
                        <button onClick={() => props.activatePage("ViewClaims")}>
                            <img 
                                src = {imgView}
                                alt = "view claims"
                            />
                        <p>View Claims</p>
                        </button>
                    </div>
                    {
                        props.currentUser.role === "proponent" ?
                        <div>
                            <button onClick={newClaimBtnHandler}>
                                <img 
                                    src = {imgCreate}
                                    alt = "create claims"
                                />
                                <p>New Claim</p>
                            </button>
                        </div>
                        : null
                    }
                    {
                        props.currentUser.role === "admin" ?
                        <div>
                            <button onClick={() => props.activatePage("UserMgt")}>
                                <img 
                                        src = {imgUsers}
                                        alt = "manage users"
                                    />
                                    <p>Users Mgt</p>
                            </button>
                        </div>
                        : null
                    }
                    <div>
                        <button onClick={logoutBtnHandler}>
                            <img 
                                src = {imgLogout}
                                alt = "logout"
                            />
                            <p>Logout</p>
                        </button>
                    </div>
                </div>
            </div>
        </nav>
    )
}

const mapStateToProps = (state) => {
    return {
        currentUser: state.loginSlice.currentUser
    }
};
const mapDispatchToProps = (dispatch) => {
    return {
        activatePage: (page) => dispatch({
            type: "ACTIVATE_PAGE",
            payload: page
        }),
        saveUserInfo: (loggedUser) => dispatch({
            type: "SAVE_USER_INFO",
            payload: loggedUser
        }),
        viewClaimDetails: (claim) => dispatch({
            type: "VIEW_CLAIM_DETAILS",
            payload: claim
        }),
        setReceiptsArray: (receiptsArray, receiptsTotalAmt) => dispatch({
            type: "SET_RECEIPTS_ARRAY",
            payload: {receiptsArray, receiptsTotalAmt}
        }),
        setUserMode: (mode) => dispatch({
            type: "SET_USER_MODE",
            payload: mode
        })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Navigation);