let savedCurrentPage = localStorage.getItem("currentPage");

const initialState = {
    appName: "Coin Retriever",
    backendPort: "https://coin-retriever.herokuapp.com/",
    activePage: savedCurrentPage === null ? "LandingPage" : savedCurrentPage,
    userMode: "create"
}

const navSlice = (state = initialState, action) => {
    switch(action.type){
        case "ACTIVATE_PAGE":
            localStorage.setItem("currentPage", "ViewClaims")
            return {
                ...state,
                activePage: action.payload
            };
        case "SET_USER_MODE":
            return {
                ...state,
                userMode: action.payload
            }
        default: return state;
    }
}

export default navSlice;